.todoList_Template {
  background-color: #fff;
  width: 87%;
  max-width: 900px;
  margin: 0 auto;
  margin-top: 4rem;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.title {
  background-color: #232323;
  color: #fff;

  padding: 1rem;
  font-size: 2rem;
  font-weight: 100;
  text-align: center;
}

.form_wrap {
  padding: 1rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.5);
}
.todos_wrap {min-height: 5rem;}
