.form {display: flex;}
.form input {
  flex: 1;   /* 버튼을 뺀 빈 공간을 모두 채운다 */
  font-size: 1.25rem;
  outline: none;
  border: none;
  border-bottom: 1px solid #ddd;
}

.createBtn {
  padding: 0.5rem 1rem;
  margin-left: 1rem;

  background-color: #232323;
  color: #fff;

  border: 1px solid #000;
  border-radius: 3px;
  font-weight: 400;
  cursor: pointer;
}